import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'DBX-SWAP',
  description:
    'The most popular AMM on BSC by user count! Earn CAKE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by DBX Rewards), NFTs, and more, on a platform you can trust.',
  image: 'https://pancakeswap.finance/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else if (path.startsWith('/nfts/collections')) {
    basePath = '/nfts/collections'
  } else if (path.startsWith('/nfts/profile')) {
    basePath = '/nfts/profile'
  } else if (path.startsWith('/pancake-squad')) {
    basePath = '/pancake-squad'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('DBX Rewards')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('DBX Rewards')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('DBX Rewards')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('DBX Rewards')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('DBX Rewards')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('DBX Rewards')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('DBX Rewards')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('DBX Rewards')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('DBX Rewards')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('DBX-SWAP')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('DBX Rewards')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('DBX-SWAP')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('DBX Rewards')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('DBX Rewards')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('DBX Rewards')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('DBX Rewards')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('DBX Rewards')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('DBX Rewards')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('DBX Rewards Info & Analytics')}`,
        description: 'View statistics for DBX Rewards exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('DBX Rewards Info & Analytics')}`,
        description: 'View statistics for DBX Rewards exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Tokens')} | ${t('DBX Rewards Info & Analytics')}`,
        description: 'View statistics for DBX Rewards exchanges.',
      }
    case '/nfts':
      return {
        title: `${t('Overview')} | ${t('DBX Rewards')}`,
      }
    case '/nfts/collections':
      return {
        title: `${t('Collections')} | ${t('DBX Rewards')}`,
      }
    case '/nfts/profile':
      return {
        title: `${t('Your Profile')} | ${t('DBX Rewards')}`,
      }
    case '/pancake-squad':
      return {
        title: `${t('Pancake Squad')} | ${t('DBX Rewards')}`,
      }
    default:
      return null
  }
}
